import React, { useState, useEffect } from 'react'
import {
    Card, Row, Col, Button, Space, Input, Table, Modal, message
} from 'antd';
import Api from '../../helpers/api';
import hasAuthObject from '../../helpers/hasAuthObject';
import handleApiError from '../../helpers/handleApiError';

import {
    PlusOutlined,
    EditOutlined,
    DeleteOutlined,
} from '@ant-design/icons';

const api = new Api;

// setup auth object config for this page
const authobjectCfg = {
    view: 'room-view', //button display detail
    create: 'room-create', //button add
    update: 'room-update', //button edit, 
    delete: 'room-delete', //button delete
}

const List = (props) => {

    const [selectedRowKeys, setSelectedRowKeys] = useState([])
    const [q, setQ] = useState('')
    const [loading, setLoading] = useState(false)
    const [data, setData] = useState([])
    const [pagination, setPagination] = useState({})

    useEffect(() => {
        fetch()
    }, [q])

    const fetch = (params = {}) => {
        // console.log('params:', params);
        setLoading(true)

        api.getRoomList({ ...params, q })
            .then(response => {
                const data = response.data;

                let pagination = {}

                pagination.current = data.current_page
                pagination.total = data.total;
                pagination.pageSize = parseInt(data.per_page);

                setData(data.data)
                setPagination(pagination)
                setLoading(false)
            })
            .catch(error => {
                setLoading(false)
                handleApiError(error, props)
            })

    }

    const handleTableChange = (paginate, filters, sorter) => {

        const pager = { ...pagination };
        pager.current = paginate.current;

        setPagination(pager)

        fetch({
            per_page: paginate.pageSize,
            page: paginate.current,
            sort_field: sorter.field,
            sort_order: sorter.order === 'ascend' ? 'asc' : 'desc',
            // ...filters,
        });
    }

    const handleEdit = () => {
        console.log('selectedRowKeys changed: ', selectedRowKeys);

        if (selectedRowKeys.length > 1) {
            Modal.warning({
                title: `Anda memilih ${selectedRowKeys.length} data, silahkan hanya pilih 1 data`,
            });
        } else {
            props.history.push(`/room/edit/${selectedRowKeys[0]}`)
        }
    }

    const handleDelete = () => {
        Modal.confirm({
            title: `Delete ${selectedRowKeys.length} data?`,
            okText: 'Yes',
            okType: 'danger',
            cancelText: 'No',
            onOk: () => {
                massDeleteData()
            },
            onCancel: () => {
                console.log('Cancel');
            },
        });
    }

    const massDeleteData = () => {
        api.massDeleteRoom({ id: selectedRowKeys })
            .then(response => {
                // console.log(response.data)
                message.success('Delete data success');
                fetch();
                setSelectedRowKeys([])
            })
            .catch(error => {
                //console.log(error.response)
                message.error('Failed to delete');
                handleApiError(error, props)
            });
    }

    //on check table item
    const onSelectChange = (selectedRowKeys) => {
        // console.log('selectedRowKeys changed: ', selectedRowKeys);
        setSelectedRowKeys(selectedRowKeys)
    }

    const rowSelection = {
        selectedRowKeys,
        onChange: onSelectChange,
        // type: 'radio'
    };

    const hasSelected = selectedRowKeys.length > 0;

    const searchData = (value) => {
        setQ(value);
    }

    const columns = [
        {
            title: 'Code',
            dataIndex: 'code',
            sorter: true,
            width: 150
        },
        {
            title: 'Name',
            dataIndex: 'name',
            sorter: true,
            width: 200
        },
        {
            title: 'Description',
            dataIndex: 'description',
            width: 300,
            sorter: true,
        },
        {
            title: 'Plant',
            dataIndex: 'plant',
            render: (plant) => {
                if (plant) { return plant.name }
            },
            sorter: true,
            width: 200
        },
        {
            title: 'Responsible Person',
            dataIndex: 'responsible_person',
            render: (responsible_person) => {
                if (responsible_person) { return responsible_person.email }
            },
            sorter: true,
            width: 200
        },
    ]

    return (
        <Card>
            <Space direction="vertical" size="large" style={{ width: '100%' }}>
                <Row>
                    <Col md={19}>
                        <Space>
                            {hasAuthObject(authobjectCfg.create) &&
                                <Button
                                    type="primary"
                                    onClick={() => { props.history.push('/room/add') }}
                                    icon={<PlusOutlined />}
                                >Add</Button>
                            }

                            {hasAuthObject(authobjectCfg.update) &&
                                <Button
                                    onClick={() => handleEdit()}
                                    disabled={!hasSelected}
                                    icon={<EditOutlined />}
                                >Edit</Button>
                            }

                            {hasAuthObject(authobjectCfg.delete) &&
                                <Button
                                    danger
                                    onClick={() => handleDelete()}
                                    disabled={!hasSelected}
                                    icon={<DeleteOutlined />}
                                >Delete</Button>
                            }
                        </Space>
                    </Col>

                    <Col md={5} >
                        <Input.Search placeholder="Search"
                            onSearch={searchData} />
                    </Col>
                </Row>
                <Row>
                    <Col lg={24} xs={24}>
                        <Table
                            loading={loading}
                            rowSelection={hasAuthObject(authobjectCfg.view || authobjectCfg.update || authobjectCfg.delete) ? rowSelection : null}
                            columns={columns}
                            rowKey={record => record.id}
                            onChange={handleTableChange}
                            dataSource={data}
                            pagination={{
                                pageSizeOptions: ['20', '50', '100', '200'],
                                showSizeChanger: true,
                                showQuickJumper: true,
                                showTotal: (total, range) => `${range[0]}-${range[1]} of ${total} items`,
                                ...pagination
                            }}
                            scroll={{ y: 300, x: 1000 }} />
                    </Col>
                </Row>
            </Space>
        </Card>
    )
}

export default List